.content-container {
  position: relative;
}

.content svg {
  width: 100%;
  position: absolute;
  bottom: 50px;
}

.social {
  width: 100%;
  position: absolute;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.social svg {
  width: 24px;
  height: 24px;
  margin: 0 12px;
  margin-bottom: 24px;
}

.social a:nth-child(1) svg path {
  fill: #26b5d9;
}
.social a:nth-child(2) svg path {
  fill: #3f3f3f;
}

.social a:nth-child(3) svg path {
  fill: #2691d9;
}
