.banner-container {
  width: 100%;
  height: 892px;
  overflow: hidden;
}

.banner-container .navbar-container {
  height: 64px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 80px;
  color: black;
}

.banner-container .navbar-container .navbar-left {
  font-size: 24px;
  display: flex;
  align-items: center;
}

.banner-container #banner-bg {
  position: absolute;
  z-index: -5;
  top: 0;
  left: 0;
  right: 0;
  background-color: whitesmoke;
  background: whitesmoke;
  background-image: radial-gradient(grey 6%, transparent 0);
  background-size: 20px 20px;
  height: 100%;
}

.banner-container .intro-content {
  position: relative;
  font-size: 20px;
  color: black;
  font-family: "Comfortaa", sans-serif;
  padding-left: 80px;
  padding-right: 80px;
  padding-top: 80px;
  line-height: 1.5;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.banner-container .intro-content .left {
  flex: 1;

  margin-right: 12px;
}
.banner-container .intro-content .left .title {
  position: relative;
  font-size: 50px;
  color: #00cba9;
  font-weight: bold;
}

.banner-container .intro-content .left .title span.emoji-wave {
  animation-name: wave-animation;
  animation-duration: 2.5s;
  animation-iteration-count: infinite;
  transform-origin: 70% 70%;
  display: inline-block;
}

@keyframes wave-animation {
  0% {
    transform: rotate(0deg);
  }
  10% {
    transform: rotate(-10deg);
  }
  20% {
    transform: rotate(12deg);
  }
  30% {
    transform: rotate(-10deg);
  }
  40% {
    transform: rotate(9deg);
  }
  50% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

.banner-container .intro-content .right {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  height: 400px;
  flex: 1;
  position: relative;
}

.frame {
  position: absolute;
  height: 285px;
  width: 395px;
  border: 30px white solid;
  border-left-width: 70px;
  border-right-width: 70px;
}

.banner-container .intro-content .right img {
  height: 300px;
}

.banner-container .diagonal-container {
  position: relative;
}

.diagonals {
  position: absolute;
  right: -500px;
  top: -60px;
  z-index: -3;
}

.banner-container .intro-content .left a {
  background-image: linear-gradient(
    to right,
    rgba(255, 255, 255, 0) 50%,
    rgba(0, 203, 169, 0.5) 50%
  );
  background-position: -0% 0;
  background-size: 200% auto;
  color: black;
  text-decoration: none;
  transition: background-position 0.3s ease-out;
}

.banner-container .intro-content .left a:hover {
  background-position: -99.99% 0;
}

.banner-container .green-outline {
  position: absolute;
  z-index: 5;
  left: 0;
  top: 523px;
}

.banner-container .dark-green-outline {
  position: absolute;
  z-index: 5;
  top: 493px;
  left: 0;
}

.banner-container .purple-outline {
  position: absolute;
  z-index: 5;
  top: 558px;
  left: 0;
}

.banner-container .white-bg {
  z-index: 3;
}

.buttons {
  margin-left: 80px;
}

.cool-button {
  font-size: 18px;
  font-weight: 600;
  background-color: white;
  padding: 10px 20px;
  margin-right: 24px;
  font-family: Courier;
  transition: transform 0.1s ease, box-shadow 0.1s ease;
}

.cool-button:hover {
  transform: translateY(-2px);
}

.cool-button:active {
  transform: translateY(2px);
  outline: none;
  color: white;
}

.cool-button:focus {
  outline: none;
}

.cool-button.one {
  color: #f45d4c;
  border: 3px solid #f45d4c;
  box-shadow: -3px 4px 0px 0px #f45d4c;
}

.cool-button.one:hover {
  box-shadow: -5px 6px 0px 0px #f45d4c;
}
.cool-button.one:active {
  box-shadow: 0px 0px 0px 0px #f45d4c;
  background-color: #f45d4c;
  color: white;
}

