.cube-container:hover {
  animation: float 1s ease-in-out infinite;
}

.cube-container {
  margin-top: 36px;
}
#cube {
  width: 48px;
  height: 48px;
  top: 10px;
  transform-style: preserve-3d;
  -moz-transform-style: preserve-3d;
  -webkit-transform-style: preserve-3d;
  transform: rotateX(-22deg) rotateY(-38deg) rotateZ(0deg) translatey(0px);
  -moz-transform: rotateX(-22deg) rotateY(-38deg) rotateZ(0deg);
  -webkit-transform: rotateX(-22deg) rotateY(-38deg) rotateZ(0deg);
  margin: auto;
  position: relative;
  -moz-transform-style: preserve-3d;
  transform-style: preserve-3d;
  -webkit-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
}
#cube > div {
  position: absolute;
  width: 48px;
  height: 48px;
  float: left;
  overflow: hidden;
  opacity: 1;

  display: flex;
  align-items: center;
  justify-content: center;

  color: whitesmoke;
  font-weight: bolder;
}
#side1 {
  font-size: 36px;
  transform: rotatex(90deg) translateX(0px) translateY(0px) translateZ(24px);
  -moz-transform: rotatex(90deg) translateX(0px) translateY(0px)
    translateZ(24px);
  -webkit-transform: rotatex(90deg) translateX(0px) translateY(0px)
    translateZ(24px);
  background-color: #60b99a;
}
#side2 {
  font-size: 24px;
  transform: rotateY(-90deg) translateX(0px) translateY(0px) translateZ(24px);
  -moz-transform: rotateY(-90deg) translateX(0px) translateY(0px)
    translateZ(24px);
  -webkit-transform: rotateY(-90deg) translateX(0px) translateY(0px)
    translateZ(24px);
  background-color: #f7a541;
}
#side3 {
  font-size: 36px;
  transform: translateX(0px) translateY(0px) translateZ(24px);
  -moz-transform: translateX(0px) translateY(0px) translateZ(24px);
  -webkit-transform: translateX(0px) translateY(0px) translateZ(24px);
  background-color: #f45d4c;
}
#side4 {
  font-size: 24px;
  transform: rotateY(90deg) translateX(0px) translateY(0px) translateZ(24px);
  -moz-transform: rotateY(90deg) translateX(0px) translateY(0px)
    translateZ(24px);
  -webkit-transform: rotateY(90deg) translateX(0px) translateY(0px)
    translateZ(24px);
  background-color: #f7a541;
}
#side5 {
  transform: rotateY(180deg) translateX(0px) translateY(0px) translateZ(24px);
  -moz-transform: rotateY(180deg) translateX(0px) translateY(0px)
    translateZ(24px);
  -webkit-transform: rotateY(180deg) translateX(0px) translateY(0px)
    translateZ(24px);
  background-color: #f7a541;
}
#side6 {
  transform: rotateX(-90deg) translateX(0px) translateY(0px) translateZ(24px);
  -moz-transform: rotateX(-90deg) translateX(0px) translateY(0px)
    translateZ(24px);
  -webkit-transform: rotateX(-90deg) translateX(0px) translateY(0px)
    translateZ(24px);
  background-color: #f7a541;
}

@keyframes float {
  0% {
    transform: translatey(0px);
  }
  50% {
    transform: translatey(-10px);
  }
  100% {
    transform: translatey(0px);
  }
}
